
import { defineComponent } from "vue";
import { fetchData, fetchingOptions } from "@/services/api";
import SearchBar from "@/components/SearchBar.vue";

export default defineComponent({
    name: "ItemsView",
    components: { SearchBar },
    data: () => {
        return {
            items: null,
            isItemsLoading: false,
            pageNumber: 1,
            totalPages: 1,
            limit: 10,
            page: 1,
            data: null,
            searchQuery: "",
            dialogSearchVisible: false,
        };
    },
    created() {
        this.searchQuery = "";
        this.getItems();
    },
    methods: {
        async getItems() {
            const data = await fetchData(fetchingOptions[this.routeName], {
                searchFor: this.searchQuery,
                page: this.page,
            });

            this.items = data.results;
            this.data = data;
            console.log(this.data);
            this.totalPages = Math.ceil(data.count / this.limit);
        },
        showSearch() {
            return (this.dialogSearchVisible = true);
        },
        changePage(pageNumber) {
            this.page = pageNumber;
        },
    },
    computed: {
        routeName() {
            return this.$route.name as string;
        },
        itemClass() {
            return this.routeName;
        },
    },
    watch: {
        routeName() {
            this.getItems();
        },
        page() {
            this.getItems();
        },
        searchVisible() {
            // this.$refs.search__input.style.display = "block";
        },
        searchQuery(val) {
            // this.$nextTick(() => {
            this.getItems();
            console.log(val);
            // });
        },
    },
});
